.animation-correct-answer {
  animation: correct-answer 2s ease-in-out both; }

@keyframes correct-answer {
  0% {
    background-color: #6e6969; }
  30% {
    background-color: #59be05; }
  60% {
    background-color: #6e6969; }
  100% {
    background-color: #59be05; } }

.animation-toast {
  animation: toast 3s ease-out both; }

@keyframes toast {
  0% {
    height: 0; }
  15% {
    height: 65px; }
  85% {
    height: 65px; }
  100% {
    height: 0;
    visibility: hidden; } }

.animation-grow {
  animation: grow 1.7s ease-in-out infinite; }

@keyframes grow {
  0%, 40%, 60%, 100% {
